import React from 'react';
import Link from 'gatsby-link';
import Helmet from 'react-helmet';
import groupBy from 'lodash/groupBy';
import map from 'lodash/map';
import reverse from 'lodash/reverse';

import formatDate from '../utils/formatDate';

const ArchivesPage = ({ data }) => {
  const yearPosts = reverse(
    map(
      groupBy(data.allMarkdownRemark.edges, post =>
        new Date(post.node.frontmatter.date).getFullYear()
      ),
      (posts, year) => ({ posts, year })
    )
  );

  return (
    <div>
      <Helmet title={`ARCHIVESPage  ${data.site.siteMetadata.title}`} />
      {map(yearPosts, ({ posts, year }) => (
        <div className="timeline" key={year}>
          <div className="timeline-item" id="timeline-example-1">
            <div className="timeline-left">
              <span className="timeline-icon" />
            </div>
            <div className="timeline-content">
              <h3 className="h5">{year}</h3>
              {posts.map(({ node: { frontmatter, fields } }) => (
                <div key={fields.slug} style={{ marginTop: '0.5rem' }}>
                  <div style={{ width: '130px', display: 'inline-block' }}>
                    <small>{formatDate(frontmatter.date)}</small>
                  </div>
                  <Link to={fields.slug}>{frontmatter.title}</Link>
                </div>
              ))}
            </div>
          </div>
        </div>
      ))}
    </div>
  );
};

export default ArchivesPage;

export const pageQuery = graphql`
  query ArchivesPageQuery {
    site {
      siteMetadata {
        title
      }
    }
    allMarkdownRemark(sort: { fields: [frontmatter___date], order: DESC }) {
      edges {
        node {
          fields {
            slug
          }
          frontmatter {
            date(formatString: "DD MMMM, YYYY")
            title
          }
        }
      }
    }
  }
`;



